<template>
  <v-row no-gutters>
    <v-col cols="12" flex>
      <cytoscape
        v-if="renderComponent"
        style="z-index:500"
        :config="config"
        :preConfig="preConfig"
        :afterCreated="afterCreated"
      />
    </v-col>
  </v-row>
</template>
<script>
// import jquery from "jquery";
// import contextMenus from "cytoscape-context-menus";
// import "cytoscape-context-menus/cytoscape-context-menus.css";
import axios from "axios";
import cola from "cytoscape-cola";
export default {
  data() {
    return {
      renderComponent: true,
      nodes: [],
      edges: []
    };
  },
  computed: {
    config() {
      return {
        elements: {
          edges: this.edges.length > 0 ? this.edges : [],
          nodes: this.nodes.length > 0 ? this.nodes : []
        },
        style: [
          {
            selector: "node",
            css: {
              content: "data(id)",
              "background-color": "#f92411",
              "text-valign": "center",
              "text-halign": "center"
            },
            style: {
              label: "data(label)",
              "text-valign": "center",
              color: "#000000",
              shape: "data(type)",
              "background-color": "data(color)"
            }
            // animate: {
            //   style: { 'background-color': 'blue' },
            // },
          },
          {
            selector: "edge",
            style: {
              width: 3,
              "line-color": "#ccc",
              "target-arrow-color": "#ccc",
              "target-arrow-shape": "triangle"
            }
          },
          {
            selector: ".center-center",
            style: {
              "text-valign": "center",
              "text-halign": "center"
            }
          }
        ],
        layout: { name: "cola", rows: 1 }
      };
    }
  },
  mounted() {
    const id = this.$route.params.id;
    this.getData(id);
  },
  methods: {
    getData(id) {
      axios
        .get(
          `?view=tables&parent=tables&child=kolom&key=table_id&where=project_id&param=${id}`
        )
        .then(res => {
          res.data.forEach(element => {
            this.nodes.push({
              data: {
                id: element.id,
                label: element.name,
                type: "rectangle",
                color: "f7f7f7"
              }
            });
            element.child.forEach(el => {
              this.nodes.push({
                data: {
                  id: el.id,
                  label: el.name,
                  color: "#fafafa",
                  parent: element.id
                }
              });
            });
          });
          this.forceRerender();
        });
    },
    preConfig(cytoscape) {
      cytoscape.use(cola);
      // it can be used both ways
      // contextMenus(cytoscape);
      // cytoscape.use(contextMenus, jquery);
    },
    afterCreated(cy) {
      // demo your core ext
    },
    forceRerender() {
      this.renderComponent = false;
      this.$nextTick(() => {
        this.renderComponent = true;
        this.i = false;
      });
    }
  }
};
</script>